import React, { createContext, useContext, useState, useCallback, useEffect } from 'react'
import { NetLedgerApi } from '../api/api'

const AppContext = createContext(null)

export function AppProvider({ children }) {
  const [isAuthenticated, setIsAuthenticated] = useState(false)
  const [serverUrl, setServerUrl] = useState('')
  const [apiKey, setApiKey] = useState('')
  const [api, setApi] = useState(null)
  const [theme, setTheme] = useState('light')
  const [error, setError] = useState(null)
  const [serverInfo, setServerInfo] = useState(null)

  // Apply theme to document
  useEffect(() => {
    document.documentElement.setAttribute('data-theme', theme)
  }, [theme])

  // Login function
  const login = useCallback(async (url, key) => {
    try {
      setError(null)
      const normalizedUrl = url.replace(/\/+$/, '')
      const apiClient = new NetLedgerApi(normalizedUrl, key)

      // Test the connection by fetching server info
      const info = await apiClient.getServerInfo()

      setServerUrl(normalizedUrl)
      setApiKey(key)
      setApi(apiClient)
      setServerInfo(info)
      setIsAuthenticated(true)

      return { success: true }
    } catch (err) {
      const message = err.message || 'Failed to connect to server'
      setError(message)
      return { success: false, error: message }
    }
  }, [])

  // Logout function
  const logout = useCallback(() => {
    setIsAuthenticated(false)
    setServerUrl('')
    setApiKey('')
    setApi(null)
    setServerInfo(null)
    setError(null)
  }, [])

  // Toggle theme
  const toggleTheme = useCallback(() => {
    setTheme(prevTheme => prevTheme === 'light' ? 'dark' : 'light')
  }, [])

  // Clear error
  const clearError = useCallback(() => {
    setError(null)
  }, [])

  // Set error (for use by components)
  const setAppError = useCallback((message) => {
    setError(message)
    // Auto-dismiss after 8 seconds
    setTimeout(() => {
      setError(prevError => prevError === message ? null : prevError)
    }, 8000)
  }, [])

  const value = {
    isAuthenticated,
    serverUrl,
    apiKey,
    api,
    theme,
    error,
    serverInfo,
    login,
    logout,
    toggleTheme,
    clearError,
    setError: setAppError
  }

  return (
    <AppContext.Provider value={value}>
      {children}
    </AppContext.Provider>
  )
}

export function useApp() {
  const context = useContext(AppContext)
  if (!context) {
    throw new Error('useApp must be used within an AppProvider')
  }
  return context
}
